body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
    width: 100%;
}
.welcomeText {
    font-size: calc(3vw + 18pt);
    text-align: center;
    color: rgb(130, 196, 226);
}

  .centerImage{
      padding-left: 25%;
      padding-right: 25%;
      width: 50%;
      -webkit-align-content: center;
              align-content: center;
  }

  .novaLogo {
    width: 60%;
    -webkit-align-self: center;
            align-self: center;
    display: block;
    margin: auto;
    max-width: 250px;
    min-width: 100px;
}

.homePageText {
    margin-left: 5%;
    margin-right: 5%;
    color: navy;
    background-color: aliceblue;
}

.mainOptionButton {
    background-color: navy;
    margin-left: 25%;
    margin-right: 25%;
    width: 50%;
    color: white;
    height: 6vw;
    min-height: 30px;
    border: 3px solid;
    border-color: #89CFF0;
    border-radius: 5px;
    font-weight: 800;
    font-size: calc(1.5vw + 8pt);
    cursor: pointer;
}
.navbar {
    border-bottom: 1px solid #e7e9ec;
    height: 60px;
    background-color: rgb(130, 196, 226);
    display: -webkit-flex;
    display: flex;
  }
  
  .navbarLogo {
      margin-top: 5px;
      margin-bottom: 5px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      border: 1px solid navy;
      background-color: white;
  }

  .homeButtons {
    width: 80%;
    display: -webkit-flex;
    display: flex;
  }

  .novaIcon {
    margin-left: 5px;
    margin-right: 5%;
  }

  .goalieIcon {
    position: absolute;
    right: 5px;
    margin-left: 2%;
  }

  .navbarButtons {
    margin-left: 1%;
    height: 100%;
    width: 33%;
    background-color: transparent;
    border: 2px white solid;
    border-radius: 5px;
    color: white;
    cursor: pointer;
  }

  .dropDown {
    position: relative;
    display: inline-block;
    float: right;
  }

  .dropDownItems {
    position: absolute;
    z-index: 1;
    display: none;
  }
  .dropDownItems a {

  }

  .dropDown:hover .dropDownItems {
    display: block;
  }
  
  @media only screen and (min-width:150px) and (max-width:750px)
  {
    .hideMobile {
      display: none !important;
    }
  }
.searchBar {
    border-radius: 5px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    background-color: white;
    border: 1px solid navy;
    width: 22%;
    position: absolute;
    right: 50px;
    height: 40px;
    padding-left: 5px;
    padding-right: 5px;
    font-size: calc( 9px + .6vw );
  }
  
  .player {
      border-right: 4px solid navy;
      padding-right: 20px;
  }

  .goalie {
      padding-left: 20px;
  }
  .searchGlass {
    border-radius: 5px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    background-color:  rgb(130, 196, 226);
    border: 1px solid navy;
    color: white;
    width: 35px;
    position: absolute;
    right: 20px;
    height: 40px;
  }

  .projectsHeader {
    vertical-align: top;
    display: inline;
    color: navy;
}

  .noPaddingRight {
    margin-right: 0px;
    font-size: calc(16px + 1.5vw);
    margin-right: 3%;
    margin: 0px;
  }

  .topBar {
    display: -webkit-flex;
    display: flex;
    margin-top: 15px;
    height: 40px;
}

tr:nth-child(even) {
    background: #CCC
}

  .statsContainer {
    margin-left:3%;
  }

  .formatStatsTable {
    background-color: white;
    margin-bottom: 25px;
    border-radius: 10px;
    width: 60%;
    margin: auto;
    margin-left: 20%;
    float: left;
    border: navy 2px solid;
}

  .formatStatsRow {
      border-bottom: 1px navy solid;
  }

  .underline {
      text-decoration: underline;
  }

  .flex {
      display: -webkit-flex;
      display: flex;
  }
